import React from 'react';
import { Link } from 'react-router-dom';
import { Helmet } from "react-helmet";

export default class Add3dViewerToWebsiteBlog extends React.Component {
    render() {
        return (
            <div className='blog-page'>
                <Helmet>
                    <meta charSet="utf-8" />
                    <title>Display products in 3D and AR on your website</title>
                    <link rel="canonical" href="https://www.ezyvue.com/blog/addingA3dViewerToAWebsite" />
                </Helmet>
                <main>
                    <h1>Display products in 3D and AR on your website</h1>
                    <img alt='ecommerce store with 3D model' src={require('assets/images/blog/watch-store.jpg')} loading='lazy' />
                    <p style={{ lineHeight: '1.0' }}>David Long<br />
                        <span style={{ fontSize: 'small' }}>4th April 2023</span>
                    </p>
                    <hr />
                    <p>
                        With interactive 3D content your customers can experience a more dynamic and interactive way of browsing your product range.
                        A 3D Viewer lets your customers interact with a realistic 3D model of a product and rotate it and zoom in for more detail. 
                        Customers with AR devices such as smartphones can also try out a product in the home before purchasing it.
                        With this step-by-step guide you&apos;ll be able to embed the Ezyvue 3D viewer a webpage and display a 3D model.
                    </p>
                    <h4>1. Create a free account</h4>
                    <p>
                        Go the <Link to='/pricing'>pricing page</Link> and sign up for the free account.
                        Click on the link in the email sent to you to activate your account and you&apos;re ready to login.
                    </p>
                    <h4>2. Create a new project and upload a 3D model</h4>
                    <p>
                        From the Project menu select New Project.
                        Fill in the name and description and select a single file or a directory if the 3D model has additional asset files such as textures.
                        Hit the Submit button and a new project will be created which can be viewed on the Manage Projects page.
                        <img alt='upload 3D model' src={require('assets/images/blog/3dviewer/newproject.jpg')} loading='lazy' />
                    </p>
                    <h4>3. Preview the file</h4>
                    <p>
                        On the Manage Projects page, click the eye icon next to the project to preview the model.
                        The preview screen background shows a placeholder image, this can be changed to an image of the model using Ezyvue Studio as we&apos;ll see later.
                        <img alt='upload 3D model' src={require('assets/images/blog/3dviewer/preview.jpg')} loading='lazy' />
                    </p>
                    <h4>4. Embed the viewer on your webpage</h4>
                    <p>
                        Go to the Integrate Viewer page from the main menu, select the project, click the copy button below the Html Code window, and paste into your page code.
                        When you update your website you should see the viewer appear.
                        <img alt='upload 3D model' src={require('assets/images/blog/3dviewer/embed.jpg')} loading='lazy' />
                    </p>
                    <h4>5. Configuring the scene using Ezyvue Studio</h4>
                    <p>
                        Ezyvue Studio allows you to configure scene settings for shadows, reflections,
                        lighting, environment, materials, and the camera. You can also use it to add features such as
                        personalization and text annotations.
                    </p>
                    <p>
                        The loading image for the viewer is only a placeholder, to change this load the Ezyvue Studio by clicking the edit button (pencil icon) next to the project on the Manage Products page.
                        When the model loads you&apos;ll notice the grid is visible, we don&apos;t want this in the image, so click the viewport tab (square icon) and untick the grid checkbox.
                        Next, click the General Settings tab (the gear icon) and adjust the camera in the main viewport then click the Take Snapshot button.
                    </p>
                    <img alt='upload 3D model' src={require('assets/images/blog/3dviewer/edit.jpg')} loading='lazy' />
                    <p>
                        To find out more about the viewer and scene settings visit the Help Center.
                    </p>
                    <hr />
                    <h3>Related blogs</h3>
                    <Link to='/blog/enhanceWebsiteWithA3DViewer'>Enhancing your online store with a 3D viewer</Link>
                    <br />
                    <br />
                </main>
            </div>
        );
    }
}